@import "https://netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css";
 
body {
  background-image: url('./images/Background.jpg');
}

 .App {
   text-align: center;
   background-repeat: no-repeat;
   background-size: 350px 350px;
   background-position: center; 
 }
 
 .App-logo {
   height: 300px;
   
 }
 
 .App-header {
   background-color: #222;
   height: 150px;
   padding: 20px;
   color: white;
 }
 
 #gmail {
   margin: -10px -100px -10px -10px;
 }
 
 .App-title {
   font-size: 1.5em;
 }
 
 
 .circular-menu {
   width: 700px;
   height: 700px;
   margin: 0 auto;
   position: relative;
   margin-top: 50px;
 }
 
 .circle {
   width: 500px;
   height: 500px;
   opacity: 0;
   display: none;
   padding: -200px;
   
   -webkit-transform: scale(0);
   -ms-transform: scale(0);
       transform: scale(0);
 
   -webkit-transition: all 0.4s ease-out;
   -o-transition: all 0.4s ease-out;
   transition: all 0.4s ease-out;
 }
 
 a {
   padding: 40px 10px;
 }
 
 .open.circle {
   opacity: 1;
   height: 700px;
   width: 700px;
 
   -webkit-transform: scale(1);
   -ms-transform: scale(1);
       transform: scale(1);
 }
 
 .circle a {
   text-decoration: none;
   color: white;
   display: block;
   height: 500px;
   width: 500px;
   line-height: 40px;
   margin-left: -20px;
   margin-top: -20px;
   /* position: absolute; */
   text-align: center;
 
 }
 
 .circle a:hover {
   color: #eef;
 }
 
 
 .menu-button {
   position: absolute;
   top: calc(90%);
   left: calc(50% - 23px);
   text-decoration: none;
   text-align: center;
   color: #000073;
   border: none;
   display: block;
   height: 40px;
   width: 40px;
   line-height: 40px;
   padding: 10px;
   background: transparent;
 }
 
 
 button:focus {outline:0;}
 
 
